"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

const steps = ["Discover", "Design", "Build", "Launch"];

export function DemoStepper() {
  const [i, setI] = useState(1);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/60 p-4 cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "stepper",
            title: "Stepper",
            tags: ["process", "steps", "progress"],
            codeUrl: "https://github.com/your/repo/path/DemoStepper.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <h2 className="text-xl font-medium">Stepper</h2>
        <div className="mt-4 flex items-center gap-2">
          {steps.map((s, idx) => (
            <div key={s} className="flex items-center gap-2">
              <div className={`h-7 w-7 rounded-full border grid place-items-center text-xs ${idx <= i ? "bg-black text-white" : ""}`}>
                {idx + 1}
              </div>
              <div className={`text-sm ${idx === i ? "font-medium" : "text-black/60"}`}>{s}</div>
              {idx < steps.length - 1 && <div className="w-10 h-[2px] bg-black/10" />}
            </div>
          ))}
        </div>
        <div className="mt-4 flex gap-2">
          <button className="px-3 py-2 rounded-md border text-sm" onClick={(ev) => (ev.stopPropagation(), setI((v) => Math.max(0, v - 1)))}>
            Back
          </button>
          <button className="px-3 py-2 rounded-md border text-sm" onClick={(ev) => (ev.stopPropagation(), setI((v) => Math.min(steps.length - 1, v + 1)))}>
            Next
          </button>
        </div>
      </div>
    </section>
  );
}
