"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoMasonryGrid() {
  const items = Array.from({ length: 14 }).map((_, i) => ({
    id: i,
    h: 80 + ((i * 37) % 120),
  }));
  return (
    <section className="mx-auto max-w-5xl px-6">
      <h2 className="text-xl font-medium mb-3">Masonry Grid (CSS columns)</h2>
      <div
        className="rounded-xl border bg-white/50 p-4 cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "masonry-grid",
            title: "MasonryGrid",
            tags: ["gallery", "masonry", "css-columns"],
            codeUrl: "https://github.com/your/repo/path/DemoMasonryGrid.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="columns-2 md:columns-3 gap-3 [column-fill:_balance]">
          {items.map((it) => (
            <div
              key={it.id}
              className="mb-3 break-inside-avoid rounded-xl border bg-white"
              style={{ height: it.h }}
            />
          ))}
        </div>
      </div>
    </section>
  );
}
