"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoInlineEditor() {
  const [editing, setEditing] = useState(false);
  const [text, setText] = useState("Click to edit this sentence.");

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/60 p-4 cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "inline-editor",
            title: "InlineEditor",
            tags: ["edit", "inline", "content"],
            codeUrl: "https://github.com/your/repo/path/DemoInlineEditor.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <h2 className="text-xl font-medium">Inline Editor</h2>
        <div className="mt-4">
          {!editing ? (
            <button
              className="text-left w-full rounded-md border bg-white px-3 py-2 text-sm hover:bg-black/5"
              onClick={(ev) => {
                ev.stopPropagation();
                setEditing(true);
              }}
            >
              {text}
            </button>
          ) : (
            <div className="flex gap-2">
              <input
                className="flex-1 rounded-md border px-3 py-2 text-sm"
                value={text}
                onChange={(e) => setText(e.target.value)}
              />
              <button className="px-3 py-2 rounded-md border text-sm" onClick={(ev) => (ev.stopPropagation(), setEditing(false))}>
                Done
              </button>
            </div>
          )}
        </div>
      </div>
    </section>
  );
}
