"use client";
import React, { useEffect, useRef, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoInfiniteList() {
  const [n, setN] = useState(20);
  const sentinelRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    const el = sentinelRef.current;
    if (!el) return;
    const io = new IntersectionObserver(
      (entries) => {
        if (entries.some((e) => e.isIntersecting)) setN((v) => Math.min(120, v + 20));
      },
      { rootMargin: "200px" }
    );
    io.observe(el);
    return () => io.disconnect();
  }, []);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div className="flex items-center justify-between mb-3">
        <h2 className="text-xl font-medium">Infinite List</h2>
        <button
          className="text-sm underline"
          onClick={(e) =>
            openSpecCard({
              featureKey: "infinite-list",
              title: "InfiniteList",
              tags: ["infinite-scroll", "intersection-observer"],
              codeUrl: "https://github.com/your/repo/path/DemoInfiniteList.tsx",
              anchor: { x: e.clientX, y: e.clientY },
            })
          }
        >
          Show Spec
        </button>
      </div>

      <div className="rounded-xl border bg-white/60 overflow-hidden">
        <div className="max-h-[260px] overflow-auto divide-y">
          {Array.from({ length: n }).map((_, i) => (
            <div key={i} className="px-4 py-3 text-sm">
              Item #{i + 1}
            </div>
          ))}
          <div ref={sentinelRef} className="px-4 py-4 text-sm text-black/60">
            Loading moreâ€¦
          </div>
        </div>
      </div>
    </section>
  );
}
