"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

type Card = { id: string; label: string };
const seed: Card[] = [
  { id: "1", label: "Component A" },
  { id: "2", label: "Component B" },
  { id: "3", label: "Component C" },
];

export function DemoSwipeCards() {
  const [cards, setCards] = useState<Card[]>(seed);

  const swipe = (dir: "left" | "right") => {
    setCards((prev) => prev.slice(1).concat(prev[0] ? [prev[0]] : []));
  };

  const top = cards[0];

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div className="flex items-center justify-between mb-3">
        <h2 className="text-xl font-medium">Swipe Cards (fake Tinder)</h2>
        <button
          className="text-sm underline"
          onClick={(e) =>
            openSpecCard({
              featureKey: "swipe-cards",
              title: "SwipeCards",
              tags: ["cards", "swipe", "chooser"],
              codeUrl: "https://github.com/your/repo/path/DemoSwipeCards.tsx",
              anchor: { x: e.clientX, y: e.clientY },
            })
          }
        >
          Show Spec
        </button>
      </div>

      <div className="relative h-[220px]">
        {cards.slice(0, 3).reverse().map((c, i) => (
          <div
            key={c.id}
            className="absolute inset-0 rounded-xl border bg-white/70 grid place-items-center text-lg font-medium"
            style={{ transform: `translateY(${i * 6}px) scale(${1 - i * 0.02})` }}
          >
            {c.label}
          </div>
        ))}
      </div>

      <div className="mt-3 flex gap-2">
        <button className="px-3 py-2 rounded-md border text-sm" onClick={() => swipe("left")}>
          Nope
        </button>
        <button className="px-3 py-2 rounded-md border text-sm" onClick={() => swipe("right")}>
          Like
        </button>
        <div className="ml-auto text-sm text-black/60">Top: {top?.label}</div>
      </div>
    </section>
  );
}
