"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoKPIStatsStrip() {
  const stats = [
    { k: "Load", v: "0.8s", s: "TTI" },
    { k: "Bounce", v: "18%", s: "Down" },
    { k: "Leads", v: "+42", s: "Week" },
    { k: "Cost", v: "$0", s: "Hosting" },
  ];
  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/60 p-4 cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "kpi-stats-strip",
            title: "KPIStatsStrip",
            tags: ["dashboard", "stats", "strip"],
            codeUrl: "https://github.com/your/repo/path/DemoKPIStatsStrip.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <h2 className="text-xl font-medium">KPI Stats Strip</h2>
        <div className="mt-4 grid grid-cols-2 md:grid-cols-4 gap-3">
          {stats.map((s) => (
            <div key={s.k} className="rounded-xl border bg-white p-3">
              <div className="text-xs uppercase tracking-wide text-black/60">{s.k}</div>
              <div className="text-2xl font-semibold">{s.v}</div>
              <div className="text-xs text-black/60">{s.s}</div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
