"use client";
import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoSkeletonLoader() {
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const t = setTimeout(() => setLoading(false), 1200);
    return () => clearTimeout(t);
  }, []);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/60 p-4 cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "skeleton-loader",
            title: "SkeletonLoader",
            tags: ["loading", "skeleton", "perceived-speed"],
            codeUrl: "https://github.com/your/repo/path/DemoSkeletonLoader.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <h2 className="text-xl font-medium">Skeleton Loader</h2>
        <p className="text-sm text-black/70 mt-1">Fake a request. Show loading polish without frameworks.</p>

        <div className="mt-4 space-y-3">
          {loading ? (
            <>
              <div className="h-4 w-40 rounded bg-black/10 animate-pulse" />
              <div className="h-24 rounded-xl bg-black/10 animate-pulse" />
              <div className="h-10 rounded bg-black/10 animate-pulse" />
            </>
          ) : (
            <>
              <div className="text-sm text-black/70">Loaded.</div>
              <div className="rounded-xl border bg-white p-4">This is the â€œrealâ€ content.</div>
            </>
          )}
        </div>
      </div>
    </section>
  );
}
