"use client";
import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoGlitchBanner() {
  const [on, setOn] = useState(false);
  useEffect(() => {
    const t = setInterval(() => setOn((v) => !v), 450);
    return () => clearInterval(t);
  }, []);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/60 p-4 cursor-pointer overflow-hidden"
        onClick={(e) =>
          openSpecCard({
            featureKey: "glitch-banner",
            title: "GlitchBanner",
            tags: ["glitch", "motion", "banner"],
            codeUrl: "https://github.com/your/repo/path/DemoGlitchBanner.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <h2 className="text-xl font-medium">Glitch Banner</h2>
        <div className="mt-4 rounded-xl border bg-white p-6 relative">
          <div className={`text-3xl font-semibold ${on ? "tracking-[0.25em]" : "tracking-tight"}`}>
            SIGNAL // CLEAR
          </div>
          <div className={`absolute inset-0 pointer-events-none ${on ? "opacity-20" : "opacity-0"} transition-opacity`}>
            <div className="absolute left-6 top-6 text-3xl font-semibold translate-x-1 translate-y-0.5">
              SIGNAL // CLEAR
            </div>
            <div className="absolute left-6 top-6 text-3xl font-semibold -translate-x-1 -translate-y-0.5">
              SIGNAL // CLEAR
            </div>
          </div>
          <p className="mt-3 text-sm text-black/70">A tasteful â€œglitchâ€ you can dial up/down.</p>
        </div>
      </div>
    </section>
  );
}
