"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoCalendarHeatmap() {
  const days = 84;
  const vals = Array.from({ length: days }).map((_, i) => (i * 17) % 5);

  const cls = (v: number) =>
    v === 0 ? "bg-black/5" : v === 1 ? "bg-black/15" : v === 2 ? "bg-black/25" : v === 3 ? "bg-black/35" : "bg-black/45";

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/60 p-4 cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "calendar-heatmap",
            title: "CalendarHeatmap",
            tags: ["visualisation", "grid", "habit"],
            codeUrl: "https://github.com/your/repo/path/DemoCalendarHeatmap.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <h2 className="text-xl font-medium">Calendar Heatmap</h2>
        <p className="text-sm text-black/70 mt-1">A tiny data-viz block without charts.</p>
        <div className="mt-4 grid grid-cols-14 gap-1">
          {vals.map((v, i) => (
            <div key={i} className={`h-3 w-3 rounded-sm ${cls(v)}`} />
          ))}
        </div>
      </div>
    </section>
  );
}
