"use client";
import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

type Theme = "paper" | "ink" | "mint" | "night";

export function DemoThemeSwitcher() {
  const [theme, setTheme] = useState<Theme>("paper");

  useEffect(() => {
    const root = document.documentElement;
    const map: Record<Theme, { bg: string; ink: string }> = {
      paper: { bg: "#f9f8f5", ink: "#0b0c0d" },
      ink: { bg: "#0b0c0d", ink: "#f9f8f5" },
      mint: { bg: "#eef7f0", ink: "#0b0c0d" },
      night: { bg: "#0a0f14", ink: "#d7e1ea" },
    };
    root.style.setProperty("--page-bg", map[theme].bg);
    root.style.setProperty("--ink", map[theme].ink);
  }, [theme]);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/60 p-4"
        onClick={(e) =>
          openSpecCard({
            featureKey: "theme-switcher",
            title: "ThemeSwitcher",
            tags: ["theme", "tokens", "dark-mode"],
            codeUrl: "https://github.com/your/repo/path/DemoThemeSwitcher.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <h2 className="text-xl font-medium">Theme Switcher</h2>
        <p className="text-sm text-black/70 mt-1">
          Click the card to open spec. Switch themes to prove token-driven design.
        </p>
        <div className="mt-3 flex flex-wrap gap-2">
          {(["paper", "ink", "mint", "night"] as Theme[]).map((t) => (
            <button
              key={t}
              className={`px-3 py-2 rounded-md border text-sm ${theme === t ? "bg-black text-white" : ""}`}
              onClick={(ev) => {
                ev.stopPropagation();
                setTheme(t);
              }}
            >
              {t}
            </button>
          ))}
        </div>
      </div>
    </section>
  );
}
