"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoCopyToClipboard() {
  const [ok, setOk] = useState(false);
  const code = "npm i && npm run dev";

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border bg-white/60 p-4 cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "copy-clipboard",
            title: "CopyToClipboard",
            tags: ["clipboard", "micro-interaction"],
            codeUrl: "https://github.com/your/repo/path/DemoCopyToClipboard.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <h2 className="text-xl font-medium">Copy to Clipboard</h2>
        <div className="mt-4 flex items-center justify-between rounded-md border bg-white px-3 py-2">
          <code className="text-sm">{code}</code>
          <button
            className="px-3 py-1 rounded-md border text-sm"
            onClick={async (ev) => {
              ev.stopPropagation();
              await navigator.clipboard.writeText(code);
              setOk(true);
              setTimeout(() => setOk(false), 1200);
            }}
          >
            {ok ? "Copied" : "Copy"}
          </button>
        </div>
      </div>
    </section>
  );
}
