"use client";
import React, { useEffect, useMemo, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

const ACTIONS = [
  { id: "open", label: "Open project" },
  { id: "duplicate", label: "Duplicate page" },
  { id: "export", label: "Export assets" },
  { id: "email", label: "Compose email" },
  { id: "theme", label: "Toggle theme" },
];

export function DemoCommandPalette() {
  const [open, setOpen] = useState(false);
  const [q, setQ] = useState("");

  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if ((e.ctrlKey || e.metaKey) && e.key.toLowerCase() === "k") {
        e.preventDefault();
        setOpen((v) => !v);
      }
      if (e.key === "Escape") setOpen(false);
    };
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, []);

  const filtered = useMemo(() => {
    const s = q.trim().toLowerCase();
    if (!s) return ACTIONS;
    return ACTIONS.filter((a) => a.label.toLowerCase().includes(s));
  }, [q]);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div className="flex items-center justify-between mb-3">
        <h2 className="text-xl font-medium">Command Palette</h2>
        <button
          className="text-sm underline"
          onClick={(e) =>
            openSpecCard({
              featureKey: "command-palette",
              title: "CommandPalette",
              tags: ["keyboard", "search", "overlay"],
              codeUrl: "https://github.com/your/repo/path/DemoCommandPalette.tsx",
              anchor: { x: e.clientX, y: e.clientY },
            })
          }
        >
          Show Spec
        </button>
      </div>

      <button className="px-3 py-2 rounded-md border text-sm" onClick={() => setOpen(true)}>
        Open (Ctrl/âŒ˜ + K)
      </button>

      {open && (
        <div className="fixed inset-0 bg-black/40 grid place-items-center z-40">
          <div className="w-[520px] max-w-[92vw] rounded-xl border bg-white shadow-xl overflow-hidden">
            <div className="p-3 border-b">
              <input
                autoFocus
                value={q}
                onChange={(e) => setQ(e.target.value)}
                placeholder="Type a commandâ€¦"
                className="w-full rounded-md border px-3 py-2 text-sm"
              />
            </div>
            <div className="max-h-[280px] overflow-auto">
              {filtered.map((a) => (
                <button
                  key={a.id}
                  className="w-full text-left px-4 py-3 text-sm hover:bg-black/5"
                  onClick={() => setOpen(false)}
                >
                  {a.label}
                </button>
              ))}
              {!filtered.length && <div className="px-4 py-6 text-sm text-black/60">No results.</div>}
            </div>
          </div>
        </div>
      )}
    </section>
  );
}
